.about_mainContainer{
    /* border: 1px solid red; */
    height: 70%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
}
.about_item{
    display: flex;
    align-items: center;
    gap: 150px;
    /* border-bottom: 1px solid;
    border-image-slice: 1;
    border-bottom-width: 3px;
    border-image-source: linear-gradient(10deg,#ff6596,#60bef8,#d88cff); */
}
.summary_item{
   /* border: 1px solid red; */
   width: 60%;
   height: 700px;
   display: flex;
   flex-direction: column;
   gap: 40px;
   padding-top: 100px;
   padding-left: 100px;
   text-align: left;
   font-size: 25px;
}
.profile{
   /* border: 1px solid red;     */
   width: 330px;
   height: 370px;
   border-radius: 50%;
   background-image: url("https://camo.githubusercontent.");
   background-position: center;
   background-size: cover;
}
.extra_info{
    /* border: 1px solid red; */
    height: 20%;
    width: 80%;
    margin: auto;
    display: flex;
    gap: 100px;
    position: relative;
    justify-content: space-evenly;
    
}
.extra_info>a>Button{
    border-bottom: 1px solid;
    border-image-slice: 1;
    border-bottom-width: 3px;
    border-image-source: linear-gradient(10deg,#ff6596,#60bef8,#d88cff);
    width:200px;
    height: auto;
    display: flex;
    padding-left:20px;
    padding-right:20px;
    font-size: 30px; 
    padding-bottom:12px 
}
