.Navbar{
    width: fit-content;
    height: 100vh;
    position:fixed;
    z-index: 100;
    border-right: 1px solid;
    border-image-slice: 5%;
    /* border-width: 3px; */
    border-image-source: linear-gradient(10deg,#ff6596,#60bef8,#d88cff);
    backdrop-filter: blur(10px);
}
 ::-webkit-scrollbar {
    width: 10px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: #f1f1f1; 
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #888; 
  } 
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #555; 
  } 

/* Item Container */


.item_container{
    border: 1px solid green;
    width: 87%;
    margin-right: 20px;
    height: 100vh;
    float:right;
    position:relative; 
    overflow-y: scroll;
}