.skill_container{
    border: 1px solid red;
    height: 100%;
}
.skill_container>.banner{
    border-bottom: 1px solid;
    border-image-slice: 1;
    border-bottom-width: 3px;
    border-image-source: linear-gradient(10deg,#ff6596,#60bef8,#d88cff);
    width: 60%;
    margin: auto;
    text-align: center;
    font-size: 55px;
    padding-top: 50px;
}