.home{
    /* border: 1px solid red; */
    height: 100%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -50px;
    gap: 100px;
   
}
.text{
    /* border: 1px solid green; */
    width: 60%;
    height: 700px;
    display: flex;
    flex-direction: column;
    align-items: left;
    gap: 50px;
}

.text>.name_Text{
    /* border: 1px solid red; */
    width: 90%;
    /* margin: auto; */
    display: flex;
    gap: 10px;
    font-size: 50px;
}
.name_Text>h1{
    color:rgb(225, 51, 51)
}
.profession>h1{
    color:rgb(225, 51, 51)

}
.text>.profession{
    /* border: 1px solid red; */
    width: fit-content;
    /* margin: auto; */
    display: flex;
    gap: 10px;
    font-size: 40px;
}
.summary{
    /* border: 1px solid red; */
    width: 95%;
    /* margin: auto; */
    font-size: 30px;
    line-height: 50px;
}
.resume{
    width: 30%;
    height:40px;
    /* margin: auto; */
}
.social{
    display: flex;
    gap: 30px;
}
.resume>a>Button{
    border: 1px solid grey;
    padding: 10px;
    width: 150px;
    border-radius: 10px;
    background-color: coral;
    color: aliceblue;
    font-size: 20px;
    font-weight: 700;
}
/* profile-image */
.profile_img{
    /* border: 1px solid blue; */
    width: 330px;
    height: 370px;
    border-radius: 50%;
    overflow: hidden;
    background-image: url('../Documents/Aniket_Bansal_Profile_Photo.JPG');
    background-position: center;
  background-size: cover;
}


/* Type Writer Effect */

.Typewriter__wrapper{
    font-weight: 900;
    background: linear-gradient(to left,#ff6596,#60bef8,#d88cff);
    background-clip: text;
    color: transparent;
}