.nav_container{
   /* border: 1px solid red; */
   height: 100%;
   width: 200px;
   display: flex;
   flex-direction: column;
   padding-bottom: 100px;
}
.nav_container>.nav_Image{
    /* border: 1px solid red; */
    height: 10%;
    font-size: 50px;
    text-align: center;
    align-items: center;
    margin-top: 50px;
    font-weight: 700;
}
.nav_container>.nav_Links{
    /* border: 1px solid red; */
    height: 100%;
    display: grid;
    grid-template-columns: repeat(1,1fr);
}
.nav_Links>.links{
    border-bottom: 1px solid;
    border-image-slice: 1;
    border-bottom-width: 3px;
    border-image-source: linear-gradient(10deg,#ff6596,#60bef8,#d88cff);
    width: 70%;
    margin: auto;
    text-align: center;
    align-self: center;
    font-size: 30px;
}
